class Product {
    id;
    title;
    category;
    price;
    image;
    description;
    rating;

    constructor(json) {
        this.id = json.id;
        this.title = json.title;
        this.category = json.category;
        this.price = json.price;
        this.image = json.image;
        this.description = json.description;
        this.rating = json.rating;
    }
}

export default Product;