class Cart {
    items;
    price;

    constructor() {
        this.items = [];
        this.price = 0;
    }

    itemExists(item_id) {
        const result = this.items.filter((item) => {
            return item.id === item_id;
        })[0];
        return result;
    }

    updateItemQuantityById(item_id, new_quantity) {
        const result = this.itemExists(item_id);

        if (result) {
            this.price -= result.price;
            this.price += result.updateQuantity(new_quantity);
        }
    }

    updateItemQuantityByReference(item, new_quantity) {
        if (item) {
            this.price -= item.price;
            this.price += item.updateQuantity(new_quantity);
        }
    }

    addItem(new_item) {
        const result = this.itemExists(new_item.id);

        if (result) { // New Item
            this.updateItemQuantityById(result.id, result.quantity + new_item.quantity)
        } else {
            this.items.push(new_item);
            this.price += new_item.price;
        }
    }

    deleteItemById(item_id) {
        for (let i = 0; i < this.items.length; i++) {
            if (this.items[i].id === item_id) {
                this.price -= this.items[i].price;
                return this.items.splice(i, 1);
            }
        }
    }

    isEmpty() {
        return (this.items.length === 0)
    }

    countItems() {
        return this.items.length;
    }
}

export default Cart;