import { createStore } from "vuex";
import Cart from "../classes/Cart"
import MessageHandler from "../classes/MessageHandler"

export default createStore({
    state: {
        navOption: 1,
        categories: [],
        cart: new Cart(),
        messageHandler: new MessageHandler()
    },
    mutations: {
        navigate(state, newNavOption) {
            state.navOption = newNavOption;
        },
        updateCategories(state, categories) {
            state.categories = categories;
        },
    },
    actions: {
        updateNavigation(context, newNavOption) {
            context.commit("navigate", newNavOption);
        },
        updateCategories(context, categories) {
            context.commit("updateCategories", categories);
        },
    },
    modules: {},
});