import Product from "./Product";

class Item {
    id;
    product;
    price;
    quantity;

    constructor(json, quantity) {
        quantity = quantity || 1;
        const product = new Product(json);
        this.id = product.id;
        this.product = product;
        this.quantity = quantity;
        this.price = product.price * quantity;
    }

    getQuantity() {
        return this.quantity;
    }

    updateQuantity(new_quantity) {
        this.quantity = new_quantity;
        this.price = this.product.price * new_quantity;

        return this.price;
    }
}

export default Item;