class MessageHandler {
    id_count;
    queue;

    constructor() {
        this.id_count = 0;
        this.queue = [];
    }

    show(title, message, time, type) {
        // time in ms
        // type: info success danger
        type = type || "info";
        const new_message = { id: this.id_count++, message, type, title };

        this.queue.push(new_message);

        setTimeout(() => { this.remove(new_message.id) }, time);
    }

    remove(message_id) {
        this.queue = this.queue.filter((message) => { return message.id !== message_id; })
    }

    getMessages() {
        return this.queue;
    }
}

export default MessageHandler;